@import url(https://fonts.googleapis.com/css?family=Nunito);
body {
  margin: 0;
  
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgba(255, 255, 255, 0.74);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#acm{
  color: #FFFFFF;
  font-family: Nunito;
  font-size: 40px;
  font-style: normal;
  font-weight: bold;
  width: 450px;
}

#nav-bar{
  align-items: center;
  background: #22ACEA;
  display: flex;
  justify-content: space-between;
  height: 75px;
}

#links-section{
  align-items: center;
  display: flex;
  justify-content: flex-end;
}

.App {
  width: 100%;
  text-align: center;
}

.selected{
  background-color: #0073a8;
}

.link{
  color: #FFFFFF;
  float: right;
  font-family: Nunito;
  font-size: 20px;
  height: 100%;
  position: relative;
  text-align: center;
  text-decoration: none;
  width: 120px;
}

.link:active{
  background-color: #0073a8;
}

.link:hover{
  font-weight: bold;
}

.title{
  color: #22ACEA;
  font-family: Nunito;
  font-size: 36px;
  font-style: bold;
  font-weight: normal;
  position: relative;
  text-align: center;
}

.buttons-section{
  width: 100%;
  align-self: right;
  margin-bottom: 20px;
  margin-top: 5px;
  position: relative;
}

.template-button{
  width: 50px;
  height: 50px;
}

.left-col { 
  float: left;
  width: 50%;
}

.right-col {
  float: right;
  width: 50%;
}


/** external styling **/
.likedButton {
  font-weight: bold;
}
 .memeImage{
  height: auto;
  max-height: 300px;
  max-width: 300px;
  object-fit: cover;
}

.memeImageOutline{
  height: 300px;
}

.memeModel{
  border-radius: 1px;
  border-width: 1px;
  border: solid;
  box-shadow: 0 4px 4px 0 rgba(0,0,0,0.2);
  display: block;
  float: left;
  height: 400px;
  margin-bottom: 20px;
  margin-left: 8px;
  margin-right: 8px;
  padding-top: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 350px;
}

.memeModel:hover{
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.unlikedButton {  }
